$Montserrat: ("Boho"), 
            ("Bubblegum"),
            ("Cantora"),
            ("Futura"),
            ("Gochi"),
            ("Inknut"),
            ("Kurale"),
            ("Bruno"),
            ("Maximian"),
            ("Rapidtech"),
            ("RedRose"),
            ("Righteous"),
            ("Roboto"),
            ("Titan");

            

@each $family in $Montserrat {
    @font-face {
        font-family: $family;
        src: url(../fonts/#{$family}/#{$family}.ttf);
    }
    @font-face {
        font-family: $family;
        src: url(../fonts/#{$family}/#{$family}.otf);
    }
}   


$FontAwesome: ("fontawesome","duotone"),
            ("fontawesome","light"),
            ("fontawesome","brands");

@each $family,$type in $FontAwesome {
    @font-face {
        font-family: 'fontawesome';
        src: url(../fonts/#{$family}/#{$family}-#{$type}.ttf);
    }
}