* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}

a {
  text-decoration: none;
}

ul {
  list-style: none;
}

.container {
  max-width: 1140px;
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  padding-right: 15px;
  padding-left: 15px;
  height: 100%;
}

@media (max-width: 1140px) {
  .container {
    max-width: 992px;
  }
}

@media (max-width: 992px) {
  .container {
    max-width: 768px;
  }
}

@media (max-width: 768px) {
  .container {
    max-width: 576px;
  }
}

@media (max-width: 576px) {
  .container {
    max-width: 100%;
  }
}

.row {
  display: flex;
  flex-wrap: wrap;
  margin-left: -15px;
  margin-right: -15px;
}

body {
  background: #000;
}

.header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: clamp(30px, calc(70vw/14.4), 100px);
  transition: 0.5s;
  z-index: 7;
}

.header::before {
  content: '';
  position: absolute;
  display: block;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  transition: 0.5s;
  background-color: white;
}

@media (max-width: 768px) {
  .header::before {
    background: #fff;
  }
}

.header-wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  height: 100%;
  position: relative;
  z-index: 6;
}

.header__list {
  display: flex;
  align-items: center;
}

@media (max-width: 768px) {
  .header__list {
    position: fixed;
    top: 0;
    left: -100%;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.8);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    z-index: 4;
    transition: 0.5s;
    align-items: start;
  }
  .header__list.active {
    left: 0;
  }
}

.header__logo {
  width: clamp(60px, calc(100vw/14.4), 120px);
  height: 100%;
  position: relative;
  z-index: 999;
}

.header__logo img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.header__link {
  display: inline-block;
  margin: 0 15px;
  text-transform: uppercase;
  color: #000;
}

@media (max-width: 768px) {
  .header__link {
    color: #fff;
  }
}

.header__burger {
  width: 30px;
  height: 20px;
  overflow: hidden;
  position: relative;
  display: none;
}

.header__burger.active::before {
  top: 8px;
  transform: rotate(45deg);
  width: 80%;
}

.header__burger.active::after {
  top: 8px;
  transform: rotate(-45deg);
  width: 80%;
}

.header__burger.active span {
  transform: scaleX(0);
}

@media (max-width: 768px) {
  .header__burger {
    display: block;
    z-index: 999;
  }
}

.header__burger::before, .header__burger::after,
.header__burger span {
  position: absolute;
  right: 0;
  display: block;
  height: 2px;
  background: blue;
  transition: 0.5s;
}

.header__burger::before, .header__burger::after {
  content: '';
}

.header__burger::before {
  top: 0;
  width: 100%;
}

.header__burger::after {
  bottom: 0;
  width: 40%;
}

.header__burger span {
  top: 8px;
  width: 70%;
  transition: 0.1s;
}

.start {
  width: 100%;
  height: 100vh;
  overflow: hidden;
}

.start-wrapper {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
}

.start-slide {
  flex: 1 0 100%;
  width: 100%;
  height: 100%;
  background: red;
}

.start-slide .container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.start-slide-wrapper {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
}

.start-pagination {
  position: absolute;
  bottom: 10px;
  left: 0;
  width: 100%;
  display: flex;
  justify-content: center;
}

.info {
  padding: 70px 0;
  width: 100%;
  height: 100vh;
}

.info__wrapper {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 25px;
}

@media (max-width: 768px) {
  .info__wrapper {
    flex-direction: column;
    align-items: center;
  }
}

.info__card {
  display: flex;
  flex-direction: column;
  padding: 15px 20px;
  gap: 30px;
  background: #fff;
  position: relative;
}

@media (min-width: 768px) {
  .info__card:hover::before {
    top: 0;
    transform: scale(1);
  }
  .info__card:hover * {
    color: #fff;
  }
}

.info__card::before {
  content: '';
  position: absolute;
  width: 100%;
  height: 100%;
  top: -40%;
  left: 0;
  background: blue;
  transition: 0.3s;
  transform: scale(0);
}

.info__title, .info__text, .info__link {
  position: relative;
  transition: 0.2s;
}

.preloader {
  /*фиксированное позиционирование*/
  position: fixed;
  /* координаты положения */
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  /* фоновый цвет элемента */
  background: #e0e0e0;
  /* размещаем блок над всеми элементами на странице (это значение должно быть больше, чем у любого другого позиционированного элемента на странице) */
  z-index: 1001;
}

.preloader__row {
  position: relative;
  top: 50%;
  left: 50%;
  width: 70px;
  height: 70px;
  margin-top: -35px;
  margin-left: -35px;
  text-align: center;
  animation: preloader-rotate 2s infinite linear;
}

.preloader__item {
  position: absolute;
  display: inline-block;
  top: 0;
  background-color: #337ab7;
  border-radius: 100%;
  width: 35px;
  height: 35px;
  animation: preloader-bounce 2s infinite ease-in-out;
}

.preloader__item:last-child {
  top: auto;
  bottom: 0;
  animation-delay: -1s;
}

@keyframes preloader-rotate {
  100% {
    transform: rotate(360deg);
  }
}

@keyframes preloader-bounce {
  0% {
    transform: scale(0);
  }
  100% {
    transform: scale(0);
  }
  50% {
    transform: scale(1);
  }
}

.loaded_hiding .preloader {
  transition: 0.3s opacity;
  opacity: 0;
}

.loaded .preloader {
  display: none;
}
