@import "./common/root" , 
        "./layout/grid" , 
        "./common/common";

body {
    background: #000;
}

.header {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: clamp(30px,calc(70vw/14.4),100px);
    transition: 0.5s;
    z-index: 7;

    &::before {
        content: '';
        position: absolute;
        display: block;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        transition: 0.5s;
        background-color: rgba(255,255,255,1);

        @include media($sm) {
            background: #fff;
        }
    }



    &-wrapper {
        display: flex;
        align-items: center;
        justify-content: space-between;
        width: 100%;
        height: 100%;
        position: relative;
        z-index: 6;
    }

    &__list {
        display: flex;
        align-items: center;

        @include media($sm){
            position: fixed;
            top: 0;
            left: -100%;
            width: 100%;
            height: 100%;
            background: rgba(0,0,0,0.8);
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            z-index: 4;
            transition: 0.5s;
            align-items: start;

            &.active {
                left: 0;
            }
        }
    }

    &__logo {
        width: clamp(60px,calc(100vw/14.4),120px);
        height: 100%;
        position: relative;
        z-index: 999;
        
        & img {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            object-fit: contain;
        }
    }

    &__link {
        display: inline-block;
        margin: 0 15px;
        text-transform: uppercase;
        color: #000;

        @include media($sm) {
            color: #fff;
        }
    }


    &__burger {
        width: 30px;
        height: 20px;
        overflow: hidden;
        position: relative;
        display: none;
        
        &.active::before {
            top: 8px;
            transform: rotate(45deg);
            width: 80%;
        }
        &.active::after {
            top: 8px;
            transform: rotate(-45deg);
            width: 80%;
        }
        &.active span {
            transform: scaleX(0);
        }
        


        @include media($sm){
            display: block;
            z-index: 999;
            
        }

        &::before, 
        &::after, 
        & span {
            position: absolute;
            right: 0;
            display: block;
            height: 2px;
            background: blue;
            transition: 0.5s;
        }
        &::before,
        &::after {
            content: '';
        }
        &::before {
            top: 0;
            width: 100%;
        }
        &::after {
            bottom: 0;
            width: 40%;
        }
        & span {
            top: 8px;
            width: 70%;
            transition: 0.1s;
            
        }
    }
   
}


.start {
    width: 100%;
    height: 100vh;
    overflow: hidden;

    &-wrapper {
        width: 100%;
        height: 100%;
        display: flex;
        align-items: center;
    }

    &-slide {
        flex: 1 0 100%;
        width: 100%;
        height: 100%;
        background: red;

        & .container {
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
        }

        &-wrapper {
            width: 100%;
            height: 100%;
            display: flex;
            align-items: center;
            justify-content: center;
            text-align: center;
        }
    }

    &-pagination {
        position: absolute;
        bottom: 10px;
        left: 0;
        width: 100%;
        display: flex;
        justify-content: center;
    }
}


.info {
    padding: 70px 0;
    width: 100%;
    height: 100vh;

    &__wrapper {
        width: 100%;
        height: 100%;
        display: flex;
        justify-content: space-between;
        align-items: center;
        gap: 25px;

        @include media($sm){
            flex-direction: column;
            align-items: center;
        }
    }

    &__card {
        display: flex;
        flex-direction: column;
        padding: 15px 20px;
        gap: 30px;
        background: #fff;
        position: relative;
        // overflow: hidden;
        

        @include media-min($sm) {
            &:hover::before {
                top: 0;
                transform: scale(1);
            }
            &:hover * {
                color: #fff;
            }
        }

        &::before {
            content: '';
            position: absolute;
            width: 100%;
            height: 100%;
            top: -40%;
            left: 0;
            background: blue;
            transition: 0.3s;
            transform: scale(0);
        }
    }

    &__title,
    &__text,
    &__link {
        position: relative;
        transition: 0.2s;
    }
}

.preloader {
      /*фиксированное позиционирование*/
  position: fixed;
  /* координаты положения */
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  /* фоновый цвет элемента */
  background: #e0e0e0;
  /* размещаем блок над всеми элементами на странице (это значение должно быть больше, чем у любого другого позиционированного элемента на странице) */
  z-index: 1001;

  &__row {
    position: relative;
    top: 50%;
    left: 50%;
    width: 70px;
    height: 70px;
    margin-top: -35px;
    margin-left: -35px;
    text-align: center;
    animation: preloader-rotate 2s infinite linear;
  }

  &__item {
    position: absolute;
    display: inline-block;
    top: 0;
    background-color: #337ab7;
    border-radius: 100%;
    width: 35px;
    height: 35px;
    animation: preloader-bounce 2s infinite ease-in-out;

    &:last-child {
        top: auto;
        bottom: 0;
        animation-delay: -1s;
    }
  }

}

@keyframes preloader-rotate {
    100% {
      transform: rotate(360deg);
    }
}
  
@keyframes preloader-bounce {
  0% {
      transform: scale(0);
  }
  100% {
      transform: scale(0);
  }
  50% {
    transform: scale(1);
  }
}

.loaded_hiding .preloader {
  transition: 0.3s opacity;
  opacity: 0;
}

.loaded .preloader {
  display: none;
}
